import axios from "axios";
import { useFormik } from "formik"
import React, { useState } from "react"
import *as Yup from'yup';
import { useNavigate } from "react-router-dom";

export default function ForgetPassword(){
    let navegate=useNavigate()
    // https://trello-dusky.vercel.app/auth/fogetPassword
    let baseUrl= 'https://trello-dusky.vercel.app';
    let[loading,setloading]=useState(false);
    let[loading2,setloading2]=useState(false);
    let[error,setErrorMsg]=useState('');
    let[error2,setErrorMsg2]=useState('');
    let[Codeflag,setCodeflag]=useState()

    let validationSchema=Yup.object({
        email:Yup.string().email("Enter valid Email").required()
    })
    let validationSchema2=Yup.object({
        resetCode:Yup.string().required().matches(/^[0-9]{8}$/ ,'Enter valid phone number'),
    })
    let form1=useFormik({
        initialValues:{
            email:'',
        },
        onSubmit:(valu)=>{
            console.log(valu)
            forgetPasswordApi(valu)
        
        },
        validationSchema,
    })

    async function forgetPasswordApi(valobj){
        setloading(true)
        let {data}= await axios.post(`${baseUrl}/auth/fogetPassword`,valobj).catch((error)=>{
            setloading(false)
            setErrorMsg(error.response.data.message);
         console.log(error)
        })
        setloading(false)
        if(data.message=='verification code send successful'){
            setCodeflag(data.message)
            navegate('/resetPassword')
        }
        console.log(data)
    }


    return(
        <div className=" flex-column align-content-center pt-5">
            {
                <form onSubmit={form1.handleSubmit}>
                <div className="my-3">
                <input onChange={form1.handleChange} type="email" name="email" className="form-control p-3" id="email" placeholder="Enter Your Email"></input>
                <p className="text-danger">{form1.errors.email}</p>
                </div>
                {error!="" ?  <div className=" alert alert-danger">{error}</div> :  ""}
                {loading ?  <button type="button" className="btn my-3"style={{ backgroundColor: " #b409ed ", color: "white" }} ><i className="fa-solid fa-spinner fa-spin text-white"></i>
                 </button> 
                :<button type="submit" className="btn my-3" style={{ backgroundColor: " #b409ed ", color: "white" }}>Send Message</button> }
                </form>
                
                
                
            }
           

           
        </div>
    )
}