
import React, { useContext, useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { taskContext } from '../../Share/task';
function UnComForuser({userData}) {
    let[loading,setLoading]=useState(false);
    let {deletTask,error,getTaskListUNCOMPforUser,uncompleteForuser} = useContext(taskContext);
   
    useEffect(() => {
        setLoading(true);
        getTaskListUNCOMPforUser();
        setLoading(false);
      }, []);
    return (
        <>
        <main className='container'>
        <div className='text-center'>
        <p className='h1 color'>Un-Compleated Tasks For User </p>
        </div>
       {uncompleteForuser.length > 0 ?
       <>
        <div className="container ">
        {error != "" ? (
            <div className=" alert alert-danger py-3">{error} cant have a prevelage to make this action</div>
          ) : (
            ""
          )}
        <div className='row g-4 pt-5'>

        {loading?( <div className="loader-container">
      	  <div className="spinner"></div>
        </div>
        ):<>
        {uncompleteForuser?.map((task,ke)=>{
          return <div key={ke} className='col-md-5'>
          <div className="card">
          <div className="card-header d-flex justify-content-between">
            <p className='h4' >{task.title}</p>
            <p className='fs-5 text-muted'>
              {task.status=='done'?
              <>
               <span className='fw-bold px-1' style={{color:'green'}}>{task.status}</span>
               <i className="fa-solid fa-circle-check" style={{color:" #349627"}}></i>
               </>:
              <>
               <span style={{color:'gray'}}>{task.status}</span>
               <i className="fas fa-tasks px-1" style={{color:" #183153"}}></i>
               </>
               }
             </p>
          </div>
          <div className="card-body py-2  ">  
          <p className="card-text text-muted">{task.description}</p>
          </div>
          <div className="card-footer ">
            <div className=' d-flex justify-content-between align-items-enter'>
              <div><i className="fa-regular fa-calendar color"></i> Deadline:{task.deadline.split('T0')[0]}</div>
              <div>
          
              <button onClick={()=>{deletTask(task._id)}} type="button" className="btn  mx-1" >
                 <i className="fa-solid fa-trash "  style={{color:' #9a0404'}}></i>
                 </button>
              <Link to={"/updateTask/" +task._id} type="button" className="btn "> <i className="fa-solid fa-pen color"></i></Link>
              </div>
              </div>
              <div>
              <i className="fa-solid fa-user-plus" style={{color: "#740666",}} ></i>
              <span style={{color:'#740666',fontWeight:'bolder'} }>AssignTo : {task?.assignTo?.userName}</span>
              </div>
          </div>
          
          </div>
  
            </div>
        })}

        </>}
        </div>

        </div>
       </>:
       <>
        <div className='text-center'>
        <p className='h1 color'>This Page To The Employee </p>
        </div>
       </>}
       
        </main>

        </>
    );
}

export default UnComForuser;