import { createContext, useEffect, useState } from "react";
import axios from "axios";
export let taskContext = createContext(null);

export function TaskContextProvider({ children }) {
  let [tasklist, setTasklist] = useState([]);
  let [tasklistcomplet, setTaskcomplet] = useState([]);
  let [uncompleteForuser, setuncompleteForuser] = useState([]);
  let [uncompleteForMang, setuncompleteForMang] = useState([]);
  //  const navigate=useNavigate()

  let baseUrl = "https://trello-dusky.vercel.app";
  let [loading1, setloading1] = useState(false);
  let [loading, setLoading] = useState(false);
  let [error, setErrorMsg] = useState([]);
  useEffect(() => {
    getTaskList();
    getTaskListComplet();
    getTaskListUNCOMPforUser();
    UnCompletForMang();
  }, []);
  //get all tasks
  async function getTaskList() {
    try {
      setLoading(true);
      let { data } = await axios.get(`${baseUrl}/task`);
      // console.log(data.tasks );
      setTasklist(data.tasks);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setErrorMsg(error.response.data?.message);
    }
  }
  //get all tasks completed
  async function getTaskListComplet() {
    try {
      setLoading(true);
      let { data } = await axios.get(`${baseUrl}/task/CompleteTasks`);
      // console.log(data.tasks );
      setTaskcomplet(data.tasks);
      setLoading(false);
    } catch (error) {
      // console.log(error);
      setErrorMsg(error?.response?.data?.message);
    }
  }
  //delete tasks globally
  async function deletTask(id) {
    try {
      setloading1(true);
      let { data } = await axios.delete(`${baseUrl}/task/${id}`, {
        headers: { authorization: localStorage.getItem("token") },
      });
      setloading1(false);
      console.log(data);
      // setmeg(data.message);
    } catch (error) {
      console.log(error);
      setloading1(false);
      setErrorMsg(error.response.data.message);
      // navigate("/home");
    }
  }
  //get all tasks uncompleted for this user
  async function getTaskListUNCOMPforUser() {
    try {
      setLoading(true);
      let { data } = await axios.get(`${baseUrl}/task/lateTasks/employee`, {
        headers: { authorization: localStorage.getItem("token") },
      });
      // console.log(data.tasks );
      setuncompleteForuser(data.tasks);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      // console.log(error);
      setErrorMsg(error.response.data.message);
    }
  }
  async function UnCompletForMang() {
    try {
      setLoading(true);
      let { data } = await axios.get(`${baseUrl}/task/lateTasks`, {
        headers: { authorization: localStorage.getItem("token") },
      });
      // console.log(data.tasks );
      setuncompleteForMang(data.tasks);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      setErrorMsg(error.response.data.message);
    }
  }

  return (
    <taskContext.Provider
      value={{
        getTaskList,
        tasklist,
        deletTask,
        error,
        getTaskListComplet,
        tasklistcomplet,
        getTaskListUNCOMPforUser,
        uncompleteForuser,
        UnCompletForMang,
        uncompleteForMang,
      }}
    >
      {children}
    </taskContext.Provider>
  );
}
