
import { Navigate, RouterProvider,createBrowserRouter, createHashRouter } from 'react-router-dom';
import Layout from './Component/Layout/Layout';
import Home from './Component/Home/Home';
import Profile from './Component/Profile/Profile';
import NotFound from './Component/NotFound/NotFound';
import CompleatedTask from './Component/CompleatedTask/CompleatedTask';
import UnComplet from './Component/CompleatedTask/UnComplet';
import TaskManger from './Component/TaskManger/TaskManger';
import TaskAssignToem from './Component/TaskAssignToem/TaskAssignToem';
import UnComForuser from './Component/CompleatedTask/UnComForuser';
import Register from './Component/Register/Register';
import Login from './Component/Login/Login';
import ProtectedRouting from './Component/ProtectRout/ProtectedRouting';
import AddTask from './Component/AddTask/AddTask';
import UpdateTask from './Component/AddTask/UpdateTask';
import jwtDecode from 'jwt-decode';
import { useEffect, useState } from 'react';
import { TaskContextProvider } from './Share/task';
import ForgetPassword from './Component/ForgetPassword/ForgetPassword';
import ResetPassword from './Component/ForgetPassword/ResetPassword';


 export default function App() { 

  let [userData, setUserData] = useState(null);

  // useEffect(() => {
  //   const token = localStorage.getItem("token");
  //   if (token) {
  //     const decodedToken = jwtDecode(token);
  //     // localStorage.setItem("userData", JSON.stringify(decodedToken));
  //     setUserData(decodedToken);
  //     saveUserData(decodedToken);
  //   } 
  // }, []);
  useEffect(()=>{
    
    if(localStorage.getItem('token') !=null ){
      //decode the token user data
      const data=jwtDecode(localStorage.getItem('token'))
      //set current user data
      setUserData(data)
      saveUserData(data);
    }
  },[])
  
// get user data
function saveUserData(userData) {
  //send currnt user data
  setUserData(userData)
}

function logOut(){
  setUserData(null);
  localStorage.removeItem('token');
  return <Navigate to='login'/>
}



  function GetDataWhileRefreshPage(props){
    if(localStorage.getItem('token')!=null){
      return <Navigate to='home'/>
    }else{
      return props.children
    }
  
  }
  

  let router=createHashRouter([
    {
      path:"/",element:<Layout logOut={logOut} userData={userData} saveUserData={saveUserData} />,children:[
         {index:true,element:<Register/>},
         {index:'register',element:<GetDataWhileRefreshPage><Register/></GetDataWhileRefreshPage>},
                   { path: "login" ,element: <Login saveUserData={saveUserData} />},

        {path:"home",element:<ProtectedRouting><Home userData={userData} saveUserData={saveUserData}/></ProtectedRouting>},

        {path:"addTask",element:<ProtectedRouting><AddTask/></ProtectedRouting>},
        {path:"updateTask/:id",element:<ProtectedRouting><UpdateTask/></ProtectedRouting>},
        {path:"taskManger",element:<ProtectedRouting><TaskManger userData={userData} /></ProtectedRouting>},
        {path:"taskEmp",element:<ProtectedRouting><TaskAssignToem userData={userData} /></ProtectedRouting>},
        {path:"complet-task",element:<ProtectedRouting><CompleatedTask userData={userData} /></ProtectedRouting>},
        {path:"Late-task(manger)",element:<ProtectedRouting><UnComplet userData={userData} /></ProtectedRouting>},
        {path:"Late-task(employee)",element:<ProtectedRouting><UnComForuser userData={userData} /></ProtectedRouting>},
        {path:"Profile",element:<ProtectedRouting><Profile userData={userData}/></ProtectedRouting>},
        { path: "ForgetPassword" ,element: <ForgetPassword />},
        { path: "resetPassword" ,element: <ResetPassword />},
        {path:"*",element:<NotFound/>},
      ]
    }
  ])
  return (

    <TaskContextProvider>
       <RouterProvider router={router}/>
    </TaskContextProvider>
   



  )
}


