import React, { useState } from "react";
import "./Register.css";
import { useFormik, validateYupSchema } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { NavLink, useNavigate } from "react-router-dom";

export default function Register() {
  let [error, setErrorMsg] = useState("");
  let [loading, setloading] = useState(false);
  let navigete = useNavigate();

  let baseUrl = "https://trello-dusky.vercel.app";
  let validate = Yup.object({
    userName: Yup.string()
      .required()
      .min(2, "main character is 2")
      .max(15, "max character is 15"),
    email: Yup.string().required().email("Enter valid eamil"),
    phone: Yup.string()
      .required()
      .matches(/^(010|011|012|015)[0-9]{8}$/, "Enter valid phone number"),
    password: Yup.string()
      .required()
      .matches(/^[a-zA-Z0-9!@#$%^&*()_-]{6,16}$/, "Enter valid password  "),
    cPassword: Yup.string()
      .required()
      .oneOf([Yup.ref("password")], "Re-Password is wrong !!"),
    gender: Yup.mixed().oneOf(["Female", "Male", "female", "male"]),
  });

  let formik = useFormik({
    initialValues: {
      userName: "",
      email: "",
      password: "",
      cPassword: "",
      phone: "",
      gender: "",
    },
    onSubmit: (value) => {
      sendDataRegister(value);
        console.log(value);
    },
    validationSchema: validate,
  });

  async function sendDataRegister(objData) {
    try {
      setloading(true);
      let { data } = await axios.post(`${baseUrl}/auth/signup`, objData);
       console.log(data);
      setloading(false);
      if (data.message == "Done") {
        //login
        // console.log('bbccc');
        navigete("/login");
      }
    } catch (error) {
      console.log("errr");
      console.log(error);
      setErrorMsg(error.response.data.message);
      setloading(false);
    }
  }

  return (
    <>
      <div className=" p-5  bg-light">
        <h1 className="h3 bg ">Register Now :</h1>
        <form onSubmit={formik.handleSubmit}>
          <div className="my-3">
            <input
              onChange={formik.handleChange}
              type="text"
              name="userName"
              className="form-control p-2 "
              id="userName"
              placeholder="userName"
            ></input>
            <p className="text-danger">{formik.errors.userName}</p>
          </div>
          <div className="my-3">
            <input
              onChange={formik.handleChange}
              type="email"
              name="email"
              className="form-control p-2"
              id="email"
              placeholder="Email"
            ></input>
            <p className="text-danger">{formik.errors.email}</p>
          </div>
          <div className="my-3">
            <input
              onChange={formik.handleChange}
              type="password"
              name="password"
              className="form-control p-2"
              id="password"
              placeholder="Password"
            ></input>
            <p className="text-danger">{formik.errors.password}</p>
          </div>
          <div className="my-3">
            <input
              onChange={formik.handleChange}
              type="password"
              name="cPassword"
              className="form-control p-2"
              id="cPassword"
              placeholder="rePassword"
            ></input>
            <p className="text-danger">{formik.errors.cPassword}</p>
          </div>
          <div className="my-3">
            <input
              onChange={formik.handleChange}
              type="text"
              name="phone"
              className="form-control p-2"
              id="phone"
              placeholder="Phone"
            ></input>
            <p className="text-danger">{formik.errors.phone}</p>
          </div>

          <select
            onChange={formik.handleChange}
            name="gender"
            className="form-select p-2 my-3"
            id="gender"
            aria-label="Default select example"
          >
            <option value="">gender</option>
            <option value="Female">Female</option>
            <option value="Male">Male</option>
            <p className="text-danger">{formik.errors.gender}</p>
          </select>

          {error != "" ? (
            <div className=" alert alert-danger">{error}</div>
          ) : (
            ""
          )}

          {loading ? (
            <button
              type="button"
              className="btn my-3"
              style={{ backgroundColor: " #b409ed ", color: "white" }}
            >
              <i className="fa-solid fa-spinner fa-spin text-white"></i>
            </button>
          ) : (
            <button
              disabled={!formik.isValid}
              type="submit"
              className="btn my-3"
              style={{ backgroundColor: " #b409ed ", color: "white" }}
            >
              Register
            </button>
          )}
        </form>
      </div>
    </>
  );
}
