import { ImExit, ImEnter } from "react-icons/im";
import { Link } from "react-router-dom";
import {Sidebar,Menu,MenuItem,} from "react-pro-sidebar";
import { FiEdit } from "react-icons/fi/";
import {FcViewDetails,FcBusinessContact,FcReading,FcBusinessman,FcAcceptDatabase,
  FcDeleteDatabase,FcTodoList,FcPlus,} from "react-icons/fc";
import "./Sidebar.css";
import React, { useEffect, useState } from "react";
import jwtDecode from "jwt-decode";

function Sidebarr({ userData, logOut, saveUserData }) {
  const token = localStorage.getItem("token");
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      const decodedToken = jwtDecode(token);
      localStorage.setItem("userData", JSON.stringify(decodedToken));
      saveUserData(decodedToken);
    }
  }, [token]);

  const [collapsed, setCollapsed] = useState(false);
  const [toggled, setToggled] = useState(false);

  const handleCollapsedChange = () => {
    setCollapsed(!collapsed);
  };
  const handleToggleSidebar = (value) => {
    setToggled(value);
  };
  return (
    <Sidebar className={` app ${toggled ? "toggled" : ""}`}style={{ height: "100%",zIndex:'999', position: "fixed",}}
      collapsed={collapsed} toggled={toggled} backgroundColor="#f9fbfe"
      aria-atomic="true"
      handleToggleSidebar={handleToggleSidebar}
      handleCollapsedChange={handleCollapsedChange}
    >
      <Menu>
        {collapsed ? (
          <MenuItem
            icon={<FcTodoList size={50} />}
            onClick={handleCollapsedChange}
          ></MenuItem>
        ) : (
          <MenuItem
            icon={<FcTodoList size={50} />}
            // suffix={<FiChevronsLeft />}
            onClick={handleCollapsedChange}
          >
            <div style={{ textTransform: "uppercase" }}>Tasks Mangement</div>
          </MenuItem>
        )}
        <hr />
      </Menu>

      <Menu>
        {userData ? (
          <>
            <MenuItem
              icon={<FcPlus size={25} />}
              component={<Link to="/addTask" />}
            >
              {" Add-Task "}
            </MenuItem>

            <MenuItem
              icon={<FcViewDetails size={40} />}
              component={<Link to="/home" />}
              onClick={() => {
                handleToggleSidebar(false);
              }}
            >
              {" All-Task "}
            </MenuItem>

            <MenuItem
              icon={<FcAcceptDatabase size={40} />}
              component={<Link to="/complet-task" />}
              onClick={() => {
                handleToggleSidebar(false);
              }}
            >
              {"complet-task "}
            </MenuItem>
            <MenuItem
              icon={<FcDeleteDatabase size={40} />}
              component={<Link to="/Late-task(manger)" />}
            >
              {"Late-task(manger) "}
            </MenuItem>
            <MenuItem
              icon={<FcDeleteDatabase size={40} />}
              component={<Link to="/Late-task(employee)" />}
            >
              {"Late-task(employee) "}
            </MenuItem>

            <MenuItem
              icon={<FcBusinessman size={40} />}
              component={<Link to="/taskManger" />}
            >
              {"Manger Tasks "}
            </MenuItem>

            <MenuItem
              icon={<FcReading size={40} />}
              component={<Link to="/taskEmp" />}
            >
              {" assign to Tasks"}
            </MenuItem>

            <MenuItem
              icon={<FcBusinessContact size={40} />}
              component={<Link to="/profile" />}
            >
              {" Profile"}
            </MenuItem>

            <MenuItem icon={<ImExit size={20} />} onClick={logOut}>
              {"LogOut "}
            </MenuItem>
          </>
        ) : (
          <>
            <MenuItem
              icon={<ImEnter size={20} />}
              component={<Link to="/Login" />}
            >
              {"Login "}
            </MenuItem>

            <MenuItem icon={<FiEdit size={20} />} component={<Link to="/" />}>
              {"Register "}
            </MenuItem>
          </>
        )}
      </Menu>
    </Sidebar>

  );
}
export default Sidebarr;
