
import React, { useEffect, useState } from "react";
import { useFormik, } from "formik";
import *as Yup from'yup';
import './task.css'
import axios from "axios";
import { Modal } from 'react-responsive-modal';

function AddTask() {
    let[error,setErrorMsg]=useState('');
    let[loading,setLoading]=useState(false);
    let[userlist,setuserlist]=useState([]);
    let [mesg, setmeg] = useState([]);
    // let[tasklist,settasklist]=useState([])


    // const [open, setOpen] = React.useState(false)
    let baseUrl= 'https://trello-dusky.vercel.app';

     let validate=Yup.object({
    title:Yup.string().required(),
    description:Yup.string().required(),
    deadline:Yup.string().required(),
    assignTo:Yup.string().required(),
    status:Yup.string(),

 })

async function getusers() {
    try {
        setLoading(true)
        let {data}=await axios.get(`${baseUrl}/user/users`,{
        headers:{authorization:localStorage.getItem('token')}
        })
        //  console.log(data.users);
         setuserlist(data.users);
        setLoading(false)
    } catch (error) {
        console.log('Erroraadd: ' + error);
        setLoading(false);
        setErrorMsg(error.response.data.message)
    }
}

 let formik=useFormik({
    initialValues:{
        title:'',
        description:'',
        deadline:'',
        assignTo:'',
        status:'',
    },
    onSubmit:(value)=>{
        addTask(value);
    },
    validationSchema:validate,
 })

async function addTask(taskData) {
    try {
        setLoading(true)
        let {data}=await axios.post(`${baseUrl}/task`,taskData,{
        headers:{authorization:localStorage.getItem('token')}
        });
         setLoading(false)
         setmeg(data.message)
    } catch (error) {

        console.log('Erroraadd: ' + error);
        setLoading(false);
        setErrorMsg(error.response.data.message)
        // navigate("/home");
        
    }
}


useEffect(()=>{
    getusers()
},[])


    return (
        <>
    <div className=" p-5  bg-light">
    <h1 className="h3 color ">Add a Tske </h1>
    <form onSubmit={formik.handleSubmit}>
  
        <div className="my-3">
        <input onChange={formik.handleChange} type="text" name="title" className="form-control p-2 " id="title" placeholder="title"></input>
        <p className="text-danger">{formik.errors.title}</p>
        </div>

        <div className="my-3">
        <textarea onChange={formik.handleChange} type="description" rows="5" name="description" className="form-control p-2" id="description" placeholder="description"></textarea>
        <p className="text-danger">{formik.errors.description}</p>
        </div>

        <div className="my-3">
        <input onChange={formik.handleChange} type="deadline" name="deadline" className="form-control p-2" id="deadline" placeholder="deadline"></input>
        <p className="text-danger">{formik.errors.deadline}</p>
        </div>

        <select name="status" className="form-select p-2 my-3 " id="status" aria-label="Default select example"
        value={formik.values.status}
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}>
        <option value="">Select an status</option>
         <option value="toDo">toDo</option>
         <option value="doing">Doing</option>
         <option value="done">Done</option>

        </select>
        {formik.touched.status && formik.errors.status ? (
         <p className="text-danger">{formik.errors.status}</p>
         ) : null}

       <select name="assignTo" className="form-select p-2 my-3 " id="assignTo" aria-label="Default select example"
        value={formik.values.assignTo}
        onChange={formik.handleChange}
               onBlur={formik.handleBlur}>

        <option value="">Select an assignee</option>
        {userlist.map(user => (
         <option className=" row justify-content-between" key={user._id} value={user._id}>
            <span style={{color:'#b409ed'}} ><span style={{color:'#b409ed'}}>Name:{user.userName}</span>/Emai:{user.email}</span>
            </option>
           ))}
        </select>

       {formik.touched.assignTo && formik.errors.assignTo ? (
         <p className="text-danger">{formik.errors.assignTo}</p>
         ) : null}
       
        
       {error != "" ? (
            <div className=" alert alert-danger">{error}</div>
          ) : (
            ""
          )}
        {mesg!=""?  <div className="alert alert-success  ">{mesg}</div> :  ""}
        {loading ? (
            <button
              type="button"
              className="btn my-3"
              style={{ backgroundColor: " #b409ed ", color: "white" }}
            >
              <i className="fa-solid fa-spinner fa-spin text-white"></i>
            </button>
          ) : (
           <>
            <button
              disabled={!formik.isValid}
              type="submit"
              className="btn my-3"
              style={{ backgroundColor: " #b409ed ", color: "white" }}
            >
              Add Task
            </button>
           </>
           
          )}
    </form>
    </div>
     </>
    );
}

export default AddTask;