import axios from "axios";
import * as Yup from "yup";
import React, { useEffect, useState } from "react";
import "./profil.css";

import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import { useFormik } from "formik";
function Profile() {
  const url = "https://trello-dusky.vercel.app/user/profilePic";
  const [image, setImage] = useState(null);
  const [imageprofile, setImageprofie] = useState("");
  const [userData, setUserData] = useState({});
  const [useRS, setuseRS] = useState([]);
  //model update user
  const [show, setShow] = useState(false);
  const handleClosee = () => setShow(false);
  const handleShoww = () => setShow(true);
  //Change Password
  const [showP, setShowP] = useState(false);
  const handleCloseeP = () => setShowP(false);
  const handleShowwP = () => setShowP(true);
  //delete user
  let [delet, settdelet] = useState();
  let [updated, setupdated] = useState({});
  //in error
  let [error, setErrorMsg] = useState("");

  let [loading, setLoading] = useState(false);
  let baseUrl = "https://trello-dusky.vercel.app";
  //update user
  let validate = Yup.object({
    userName: Yup.string().required(),
    age: Yup.string().required(),
    phone: Yup.string().required(),
    email: Yup.string(),
  });

  const formik = useFormik({
    initialValues: {
      userName: userData.userName,
      age: userData.age,
      phone: userData.phone,
      email: userData.email,
    },

    onSubmit: (values) => {
      console.log(values);
      updateUSER(values);
    },
    validationSchema: validate,
    enableReinitialize: true,
  });
  async function updateUSER(body) {
    try {
      setLoading(true);
      let { data } = await axios.put(`${baseUrl}/user/`, body, {
        headers: { authorization: localStorage.getItem("token") },
      });
      setLoading(false);
      console.log(data);
      setupdated(data);
    } catch (error) {
      console.log(error);
      setLoading(false);
      setErrorMsg(error.response.data.message);
    }
    //delete users
  }
  //////////////////////////////ChangePassword////////////
  let validatePasword = Yup.object({
    oldPassword: Yup.string().required(),
    newPassword: Yup.string()
      .required()
      .matches(/^[a-zA-Z0-9!@#$%^&*()_-]{6,16}$/, "Enter valid password  "),
  });

  const formikChangePasssword = useFormik({
    initialValues: {
      oldPassword: "",
      newPassword: "",
    },

    onSubmit: (values) => {
      console.log(values);
      Changepaswword(values);
    },
    validationSchema: validatePasword,
    enableReinitialize: true,
  });
  let [error2, setErrorMsg2] = useState("");
  let [error3, setErrorMsg3] = useState("");

  const [ChangePassword, setChangePassword] = useState();
  async function Changepaswword(body) {
    try {
      setLoading(true);
      let { data } = await axios.patch(
        `https://trello-dusky.vercel.app/user/change-password`,
        body,
        {
          headers: { authorization: localStorage.getItem("token") },
        }
      );
      setLoading(false);
      console.log(data);
      setChangePassword(data.message);
      if (data.validationResult.length > 0) {
        setErrorMsg3(data.validationResult[0].message);
      }
      
    } catch (error) {
      console.log(error);
      setLoading(false);
      setErrorMsg2(error.response.data.message);
    }
    console.log(error2);
  }

  async function updateUSER(body) {
    try {
      setLoading(true);
      let { data } = await axios.put(`${baseUrl}/user/`, body, {
        headers: { authorization: localStorage.getItem("token") },
      });
      setLoading(false);
      console.log(data);
      setupdated(data);
    } catch (error) {
      console.log(error);
      setLoading(false);
      setErrorMsg(error.response.data.message);
    }
    //delete users
  }

  //delete users
  async function deletUSER(idUser) {
    try {
      setLoading(true);
      let { data } = await axios.delete(`${baseUrl}/user/ADdelet/${idUser}`, {
        headers: { authorization: localStorage.getItem("token") },
      });
      console.log(data);
      settdelet(data.message);
    } catch (error) {
      console.log(error);
      setLoading(false);
      setErrorMsg(error.response.data.message);
    }

    // navigate("/home");
  }
  ///IIIIIIImmmmmaaagee///////////////////////////////
  const handleFileUpload = async (event) => {
    // get the selected file from the input
    const file = event.target.files[0];
    // create a new FormData object and append the file to it
    const formData = new FormData();
    formData.append("profilePic", file);
    // make a POST request to the File Upload API with the FormData object and Rapid API headers
    try {
      const { data } = await axios.post(url, formData, {
        headers: { authorization: localStorage.getItem("token") },
      });
      console.log(data);
      setImageprofie(data.result.profilePicture.secure_url);
    } catch (error) {
      console.log(error);
    }
  };

  const fetchProfile = async () => {
    setLoading(true);
    const { data } = await axios
      .get(`https://trello-dusky.vercel.app/user/Oneuser`, {
        headers: { authorization: localStorage.getItem("token") },
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
        setErrorMsg(err.response.data.message);
      });
    setLoading(false);
    console.log(data);
    setUserData(data.result);
    setImageprofie(data?.result.profilePicture.secure_url);
  };
  //all users
  const users = async () => {
    setLoading(true);
    const { data } = await axios
      .get(`https://trello-dusky.vercel.app/user/users`, {
        headers: { authorization: localStorage.getItem("token") },
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
        setErrorMsg(err.response.data.message);
      });
    setLoading(false);
    console.log(data);
    setuseRS(data?.users);
  };

  // useEffect(() => {
  //   users();
  //   fetchProfile();
  // }, [updated]);
  // useEffect(() => {

  // }, []);

  useEffect(() => {
    users();
    if (updated) {
      fetchProfile();
    }
  }, [updated, delet, error]);
  return (
    <>
      <div class="container emp-profile shadow-lg">
        <form method="post">
          <div class="row">
            <div class="col-md-4 ">
              <div class="profile-img">
                <img
                  className="col-5"
                  style={{ objectFit: "cover", objectPosition: "center" }}
                  src={imageprofile}
                  alt="img-profile"
                />
                <div className="file btn  btn-lg btn-primary">
                  {" "}
                  Change Photo
                  <input type="file" name="file" onChange={handleFileUpload} />
                </div>
              </div>
            </div>
            <div class="col-md-6">
              <div class="profile-head pt-4">
                <h3>{userData?.userName}</h3>
                <h6>
                  {userData.isAdmain == true
                    ? "(You Are The manger)"
                    : "(You Are The EMployee in This System)"}
                </h6>
                <div class="row py-3">
                  {/* <div class="col-md-3">
                    <label>User_Id</label>
                  </div>
                  <div class="col-md-6">
                    <p>{userData?._id}</p>
                  </div> */}
                </div>
                <div class="row">
                  <div class="col-md-3">
                    <label>Email</label>
                  </div>
                  <div class="col-md-6">
                    <p>{userData?.email}</p>
                  </div>
                </div>
                <div class="row">
                  <div class="col-md-3">
                    <label>Phone</label>
                  </div>
                  <div class="col-md-6">
                    <p>{userData?.phone}</p>
                  </div>
                </div>
                <div class="row">
                  <div class="col-md-5">
                    <Button variant=" bt-m" onClick={handleShoww}>
                      Edite your Profile{" "}
                      <i className="fa-solid fa-pen text-waring cursor  " />
                    </Button>
                  </div>
                  <>
                    <Modal show={show} onHide={handleClosee}>
                      <Modal.Header closeButton>
                        <Modal.Title>Update User</Modal.Title>
                      </Modal.Header>
                      <Modal.Body>
                        <Form onSubmit={formik.handleSubmit}>
                          <Form.Group
                            className="mb-3"
                            controlId="exampleForm.ControlInput1"
                          >
                            <Form.Label>User Name</Form.Label>
                            <Form.Control
                              onChange={formik.handleChange}
                              value={formik.values.userName}
                              type="text"
                              name="userName"
                              placeholder="your name"
                              autoFocus
                            />
                          </Form.Group>

                          <Form.Group
                            className="mb-3"
                            controlId="exampleForm.ControlInput1"
                          >
                            <Form.Label>Email</Form.Label>
                            <Form.Control
                              onChange={formik.handleChange}
                              value={formik.values.email}
                              type="email"
                              name="email"
                              placeholder="name@example.com"
                              autoFocus
                            />
                          </Form.Group>

                          <Form.Group
                            className="mb-3"
                            controlId="exampleForm.ControlInput1"
                          >
                            <Form.Label>Phone</Form.Label>
                            <Form.Control
                              onChange={formik.handleChange}
                              value={formik.values.phone}
                              type="text"
                              name="phone"
                              placeholder="01122253244"
                              autoFocus
                            />
                          </Form.Group>

                          <Modal.Footer>
                            <Button variant="secondary" onClick={handleClosee}>
                              Close
                            </Button>

                            <Button
                              className="bt-m"
                              disabled={!formik.isValid}
                              type="submit"
                              onClick={handleClosee}
                            >
                              Save Changes
                            </Button>
                          </Modal.Footer>
                        </Form>
                      </Modal.Body>
                    </Modal>
                  </>
                </div>
              </div>
            </div>
          </div>
          {error && <p className="error">{error}</p>}
          {userData.isAdmain ? (
            <>
              <div class="row">
                <div className="d-flex justify-content-between">
                  <h3 className="shadow-sm text-muted">Users in this system</h3>
                  {delet ? (
                    <div
                      class="alert alert-danger d-flex align-items-center"
                      role="alert"
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="24"
                        height="24"
                        fill="currentColor"
                        class="bi bi-exclamation-triangle-fill flex-shrink-0 me-2"
                        viewBox="0 0 16 16"
                        role="img"
                        aria-label="Warning:"
                      >
                        <path d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767L8.982 1.566zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5zm.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2z" />
                      </svg>
                      <div>{delet}</div>
                    </div>
                  ) : (
                    ""
                  )}

                  {/* {delet ? <div className=" alert alert-danger ">{delet}</div> : ""} */}
                  {error != "" ? (
                    <div className=" alert alert-danger">{error}</div>
                  ) : (
                    ""
                  )}
                </div>
                {useRS ? (
                  <table className="">
                    <thead>
                      <tr>
                        <th scope="col text-muted">UserName</th>
                        <th scope="col text-muted">Eamil</th>
                        <th scope="col text-muted">Phone</th>
                        <th scope="col text-muted">Delete</th>
                      </tr>
                    </thead>
                    <tbody>
                      {useRS?.map((el, index) => {
                        return (
                          <tr key={index}>
                            {/* <td>
                          <p>{el._id}</p>
                        </td> */}
                            <td>{el.userName}</td>
                            <td>
                              <p>{el.email}</p>
                            </td>
                            <td>{el.phone} </td>
                            <td>
                              {" Delet"}
                              <i
                                onClick={() => deletUSER(el._id)}
                                className="fa-solid fa-trash text-danger cursor btn "
                              ></i>
                            </td>
                          </tr>
                        );
                      })}

                      <tr className="">
                        <td colSpan={3} className="border fw-bold">
                          Nunber Of users
                        </td>
                        <td colSpan={1} className="border fw-bold">
                          {useRS?.length}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                ) : (
                  ""
                )}
              </div>
            </>
          ) : (
            ""
          )}
        </form>
        <div className=" py-5">
          <div class="row  flex-column">
            <div class="col-md-5">
              <Button variant=" bt-m" onClick={handleShowwP}>
                ChangePassword{" "}
                <i className="fa-solid fa-pen text-waring cursor  " />
              </Button>
            </div>
            <>
              <Modal show={showP} onHide={handleCloseeP}>
                <Modal.Header closeButton>
                  <Modal.Title>ChangePassword</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <Form onSubmit={formikChangePasssword.handleSubmit}>
                    <Form.Group
                      className="mb-3"
                      controlId="exampleForm.ControlInput1"
                    >
                      <Form.Label>oldPassword</Form.Label>
                      <Form.Control
                        onChange={formikChangePasssword.handleChange}
                        value={formikChangePasssword.values.oldPassword}
                        type="password"
                        name="oldPassword"
                        placeholder="oldPassword"
                        autoFocus
                      />
                        <p className="text-danger">{formikChangePasssword.errors.oldPassword}</p>
                    </Form.Group>

                    <Form.Group
                      className="mb-3"
                      controlId="exampleForm.ControlInput1"
                    >
                      <Form.Label>newPassword</Form.Label>
                      <Form.Control
                        onChange={formikChangePasssword.handleChange}
                        value={formikChangePasssword.values.newPassword}
                        type="password"
                        name="newPassword"
                        placeholder="A1235hgf$@00"
                        autoFocus
                      />
                       <p className="text-danger">{formikChangePasssword.errors.newPassword}</p>
                    </Form.Group>
                    {ChangePassword != "" ? (
                      <div className="alert alert-success  ">{ChangePassword}</div>
                    ) : (
                      ""
                    )}
                    {error2 != "" ? (
                      <div className=" alert alert-danger">{error2}</div>
                    ) : (
                      ""
                    )}
                    {error3 != "" ? (
                      <div className=" alert alert-danger">{error3}</div>
                    ) : (
                      ""
                    )}
                  

                    <Modal.Footer>
                      <Button variant="secondary" onClick={handleCloseeP}>
                        Close
                      </Button>

                      <Button
                        className="bt-m"
                        disabled={!formikChangePasssword.isValid}
                        type="submit"
                        onClick={handleClosee}
                      >
                        Update Password
                      </Button>
                    </Modal.Footer>
                  </Form>
                </Modal.Body>
              </Modal>
            </>
          </div>
        </div>
      </div>
    </>
  );
}

export default Profile;
