import React,{Fragment} from "react";
import { Outlet } from "react-router-dom";
// import Sidebar from "../Sidebar/Sidebarr"
import Sidebarr from "../Sidebar/Sidebarr";


export default function Layout({userData,logOut,saveUserData}){
    return(
     <>

      <div className="container-fluid py-5">
  <div className="row">
    <div className="col-md-1 col-2">
      
    <Fragment>
    <Sidebarr  logOut={logOut} userData={userData} saveUserData={saveUserData} />
    </Fragment>
     
      
    </div>
    <div className="col-1"></div>
    <div className="col-md-10 col-9" id="main" >
    <Outlet/>
    </div>
  </div>
</div>

     </>

   
      
    );
}


